<template>
	<tooltip text="Logout">
		<div @click="logout" style="cursor: pointer" >
			<slot name="default" />
		</div>
	</tooltip>
</template>

<script>
import { mapActions } from "vuex";
import Tooltip from "./Tooltip.vue";
export default {
	components: { Tooltip },
	methods: {
		...mapActions("common", ["clearCurrentUser"]),
		logout() {
			this.clearCurrentUser();
			this.$router.push({ path: "/login" });
		},
	},
};
</script>

